
#tab_list {
    // justify-content: center;
    .tab {
        margin-bottom: 20px;
        .tab-content {
            border-radius: 6px;
            .tab-header {
                height: 50px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 0 20px;
                cursor: pointer;
                border: 1px solid #DDd;
                background-color: #FFF;
                border-radius: 5px;
                .tab-title {
                    font-size: 16px;
                    margin: 0;
                }
                .tab-control {
                    display: flex;
                    align-items: center;
                }
                .tab-icon {
                    &.flip-icon {
                        i {
                            transform: rotate(180deg);
                        }
                    }
                }
            }
        }

        .tab-body {
            padding: 30px 20px;
            display: none;
        }

        &.open-tab {
            .tab-content {
                .tab-body {
                    display: block;
                }
            }
        }
    }
}




.empty-containers {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    padding: 100px 0;
    border-radius: 5px;
    background-color: #FFF;
    .add {
        user-select: none;
        cursor: pointer;
        display: inline-block;
        transition: color 0.2s ease-in-out;
        color: #666;
        i {
            color: inherit;
        }
        &:hover {
            color: #000;
        }
    }
}

.no-sections {
    padding: 50px;
    background-color: #FFF;
    border-radius: 3px;
    color: #666;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
}


.wrapper_section_list {
    min-height: 100px;
}

.section-sortable-placeholder {
    background-color: #CCC;
    min-height: 100px;
    margin: 15px 0;
    border-radius: 5px;
}
.section-component {
    border: 1px solid #BBB;
    background-color: #EEE;
    margin-bottom: 30px;
    border-radius: 5px;
    transition: background-color 1s ease-in-out, transform 0.2s cubic-bezier(0.89, 1.41, 0.1, 1.09);
    .section-component-header {
        height: 50px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px;
        background-color: #d1d1d1;
        border-radius: 3px;
        cursor: move;
        transition: background-color 1s ease-in-out;
        .section-component-title {
            .title {
                cursor: pointer;
                margin: 0;
                display: inline-block;
                margin-right: 10px;
            }
        }
        .section-component-control-view {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 7px;
            border-radius: 3px;
            min-width: 100px;
            .btn-menu, .btn-moving, .section-component-toggle {
                cursor: pointer;
                i {
                    transition: all 0.2s ease-in-out;
                }
                &:hover {
                    i {
                        color: #222;
                    }
                }
            }
            .btns-moving {
                display: flex;
                align-items: center;
                .btn {
                    border: 1px solid #BBB !important;
                    padding: 4px 8px !important;
                    font-size: 12px !important;
                }
            }
            
        }
    }
    .section-component-wrapper-forms {
        display: none;
    }
    &.container_section {
        .wrapper-sections {
            .container-section-title {
                padding-bottom: 8px;
                margin-bottom: 20px;
                position: relative;
                display: inline-block;
                &:after {
                    content: '';
                    height: 2px;
                    width: 70%;
                    background-color: #3498db;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                }
            }
            .container-inner {
                border: 1px solid #BBB;
                padding: 20px;
                border-radius: 6px;
                &:not(:last-of-type) {
                    margin-bottom: 25px;
                }
            }
        }
    }

    &.moving {
        transform: scale(0.85);
    }
    &.new {
        background-color: #c6e8f7;
        .section-component-header {
            background-color: #deeef5;
        }
    }
    &.deleting {
        transition: transform 0.3s ease-in-out;
        transform: scale(0);
    }
}

.section-component-form {
    padding: 15px;
}

.add_new_widget {
    .btn {
        display: block;
        width: 100%;
    }
}


#section_class_list {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 6666;
    background-color: rgba(#000, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.15s ease-in-out;
    transform: scale(0);
    opacity: 0;
    &.open {
        transform: scale(1);
        opacity: 1;
    }
    .sections-view {
        width: 600px;
        .sections-content {
            background-color: #FFf;
            border-radius: 6px;
            border: 1px solid #EEE;
            .sections-search {
                padding: 15px;
                border-bottom: 1px solid #EEE;
            }
            .section-list {
                display: flex;
                flex-wrap: wrap;
                padding: 10px;
                max-height: 500px;
                overflow: auto;
                .section-box {
                    width: 115px;
                    min-height: 115px;
                    margin: 10px;
                    padding: 10px;
                    border: 1px solid #DDD;
                    border-radius: 5px;
                    cursor: pointer;
                    text-align: center;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;
                    transition: all 0.2s ease-in-out;
                    &:hover {
                        border-color: #555;
                    }
                    .section-image {
                        width: 100px;
                        height: 100px;
                        .image {
                            width: 100%;
                            height: 100%;
                        }
                    }
                    .section-title {
                        color: #222;
                        font-weight: 600;
                        &.has-image {
                            max-width: 100%;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            font-size: 12px;
                            margin-top: 10px;
                        }
                        &.no-image {
                            font-size: 13px;
                            word-break: break-word;

                        }
                    }
                }
            }
        }
    }
}


#widget_class_list {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 6666;
    background-color: rgba(#000, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.15s ease-in-out;
    transform: scale(0);
    opacity: 0;
    &.open {
        transform: scale(1);
        opacity: 1;
    }
    .widgets-view {
        width: 400px;
        min-height: 400px;
        margin-top: 50px;
        .widgets-content {
            background-color: #FFf;
            border-radius: 6px;
            border: 1px solid #EEE;
            .widgets-search {
                padding: 15px;
                border-bottom: 1px solid #EEE;
            }
            .widget-groups {
                padding: 15px;
                .widget-list {
                    display: flex;
                    flex-wrap: wrap;
                    margin-bottom: 15px;
                    .widget-box {
                        width: 102px;
                        min-height: 102px;
                        margin: 10px;
                        padding: 10px;
                        border: 1px solid #DDD;
                        border-radius: 5px;
                        cursor: pointer;
                        text-align: center;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        flex-direction: column;
                        transition: all 0.2s ease-in-out;
                        &:hover {
                            border-color: #555;
                            .widget-icon {
                                i {
                                    color: #222;
                                }
                            }
                        }
                        .widget-icon {
                            margin-bottom: 5px;
                            i {
                                transition: all 0.2s ease-in-out;
                                font-size: 20px;
                            }
                        }
                        .widget-title {
                            color: #222;
                            font-weight: 600;
                            font-size: 14px;
                            word-break: break-word;
                        }
                    }
                }
            }
        }
    }
}


.hide-box {
    transform: scale(0);
    position: absolute;
    z-index: -1;
}


.form-separator {
    display: block;
    width: 100%;
    height: 2px;
    margin: {
        top: 30px;
        bottom: 30px;
    }
    background-color: #DDD;
    &.inner {
        margin: {
            left: auto;
            right: auto;
        }
        width: 95%;
    }
    &.half {
        margin: {
            left: auto;
            right: auto;
        }
        width: 50%;
    }
}


.setting-box {
    .setting-box-label {
        padding-bottom: 8px;
        margin-bottom: 20px;
        position: relative;
        display: inline-block;
        &:after {
            content: '';
            height: 2px;
            width: 70%;
            background-color: #3498db;
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }
    .setting-boxs {
        padding: {
            left: 10px;
            right: 10px;
        }
        &:not(.last) {
            border-bottom: 1px solid #BBB;
            padding-bottom: 10px;
            margin-bottom: 30px;
        }
    }
}